<template>
  <div id="one2">
    <div>
      <mt-header fixed style="background:#ccc;width:100%;height:50px">
        <router-link to="/" slot="left">
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>
    </div>
    <div style="margin-top:60px">
      <p style="line-height:50px;color:#000">
        
      </p>
    </div>
    <div v-if="list">
      <div style="margin-bottom:20px;height:130px" v-for="(i,$index) in list" :key="$index">
        <div style="float:left;width:50%;">
          <img :src="i.url" style="width:130px;height:130px;" />
        </div>
        <div style="float:right;width:50%;">
          <p style="font-size:15px;color:#000">{{i.foodnm}}</p>
          <p style="font-size:13px;color:#ccc;line-height:40px">
            {{i.yuexiao}}
            <span>{{i.haopin}}</span>
          </p>
          <p style="color:red">
            {{i.price}}
            <span style="display:inline-block;line-height:60px">* {{i.num}}</span>
          </p>
        </div>
      </div>
    </div>
    <div style="position:fixed;bottom:0;left:0;overflow:hidden;width:100%;height:80px">
      <div
        style="float:left;color:#fff;width:70%;height:80px;line-height:80px;background:rgba(0,0,0,.5);text-align:center;line-height:80px"
      >
        :{{totalPrice}}
        <router-view></router-view>
      </div>
      <div
        style="float:left;width:30%;height:80px;text-align:center;line-height:80px;background:green"
      >
        <a style="color:#fff;"></a>
      </div>
    </div>
  </div>
</template>
<script>
// import on1 from "../components/on1.vue";
import { mapGetters } from "vuex";
export default {
  // components:{
  //   on1
  // },
  name: "one2",
  data() {
    return {
      list: ""
    };
  },
  computed: {
    //  ...mapState(['orderList']),
    ...mapGetters(["totalPrice"])
  },
  created() {
    this.list = this.$store.state.orderList;
    console.log(this.list);
  }
};
</script>
<style>
</style>


