import Vue from 'vue'
import Router from 'vue-router'
import one1 from './one1.vue'
import one2 from './one2.vue'


Vue.use(Router)

var routes = [
    {
        path: '/', name: 'one1', component: one1,

    },
    {
        path: '/one2', name: 'one2', component: one2,
    }
]



export default new Router({
    routes
})



