<template>
  <div id="one1">
    <div>
      <router-view></router-view>
    </div>
    <div class="background:#fff;border-bottom:1px #000 solid;height:50px;width:100%;">
      <mt-header fixed style="background:#fff;color:#000;border-bottom:1px #000 solid;height:50px">
        <router-link to="/" slot="left" style="color:blue">
          <mt-button icon="back" style="color:#000"></mt-button>
        </router-link>
        <mt-button icon="more" slot="right" class="color:#000"></mt-button>
      </mt-header>
    </div>
    <div style="margin-top:50px;overflow:hidden">
      <div style="float:left;width:20%">
        <div>
          <div v-for="(i,$index) in food_name" @click="type=i" :key="$index">
            <p style="line-height:30px;text-align:center">{{i}}</p>
          </div>
        </div>
      </div>
      <div style="width:80%;float:left">
        <div v-for="(i,$index) in arr" :key="$index" style="overflow:hidden;">
          <div v-if="i.type==type" style="margin-bottom:20px">
            <div style="float:left;width:40%;">
              <img :src="i.img" style="width:130px;height:130px;" />
            </div>
            <div style="float:right;width:50%;">
              <p style="font-size:15px;color:#000">{{i.foodnm}}</p>
              <p style="font-size:13px;color:#ccc;line-height:40px">
                {{i.yuexiao}}
                <span>{{i.haopin}}</span>
              </p>
              <p style="color:red">
                {{i.price}}
                <span style="display:inline-block;line-height:60px">
                  <on1 v-model="i.num"></on1>
                </span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div style="position:fixed;bottom:0;left:0;overflow:hidden;width:100%;height:80px">
      <div
        style="float:left;width:70%;height:80px;line-height:80px;background:rgba(0,0,0,.5);text-align:center;line-height:80px;color:#fff"
      >
        :{{totalPrice}}
        <router-view></router-view>
      </div>
      <div
        style="float:left;width:30%;height:80px;text-align:center;line-height:80px;background:green;color:#fff"
      >
      <router-link :to="{path:path}">
        
      </router-link>
      </div>
    </div>
  </div>
</template>
<script>
import on1 from "./on1.vue";
import {mapState,mapGetters} from 'vuex'
export default {
  name: "one1",
  components: {
    on1
  },
  watch:{
    'arr':{
      handler(v1){
        this.arr1=[];
        v1.forEach(el=>{
          if(el.num){
            this.arr1.push(el)
          }
        })
        this.$store.commit('addOrder',this.arr1)
      },
      deep:true
    }
  },
  data() {
    return {
      path: "/one2",
      type: "",
      arr: "",
      arrlist: [],
      food_name: [
        "",
      ]
    };
  },
  // 
  computed: {
     ...mapState(['orderList']),
    ...mapGetters(['totalPrice']),
  },
  created() {
    this.$http.get("data.json").then(data => {
      this.arr = data.data.arr;
    });
  },
  methods:{
    addItem(item,ev){
      if(item.num){
        item.num=ev
      }else{
        this.$set(item,'num',ev)
      }
    }
  }
};
</script>
<style>
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style-type: none;
}
</style>



